/* Grid System */


//--------------------------------------------------//
//                  Calculations                    //
// . . . . . . . . . . . . . . . . . . . . . . . . .//
//                                                  //
//      scw = (100 – (m * (mc – 1))) / mc           //
//                                                  //
//      where:                                      //
//      scw = single column width                   //
//      m = margin (1.6%)                           //
//      mc = maximum columns (12)                   //
//                                                  //
// . . . . . . . . . . . . . . . . . . . . . . . . .//
//                                                  //
//      cw = (scw * cs) + (m * (cs – 1))            //
//                                                  //
//      where:                                      //
//      cw = column width                           //
//      scw = single column width (6.86666666667%)  //
//      cs = column span (1-12)                     //
//      m = margin (1.6%)                           //
//                                                  //
//                                                  //
//--------------------------------------------------//


//----------------
//  Grid structure
//----------------

.row {
    @include clearfix();
}

.column {
    position: relative;
    float: left;
    display: block;
}

.column + .column {
    margin-left: 1.6%;
}

.column-1 {
    width: 6.86666666667%;
}

.column-2 {
    width: 15.3333333333%;
}

.column-3 {
    width: 23.8%;
}

.column-4 {
    width: 32.2666666667%;
}

.column-5 {
    width: 40.7333333333%;
}

.column-6 {
    width: 49.2%;
}

.column-7 {
    width: 57.6666666667%;
}

.column-8 {
    width: 66.1333333333%;
}

.column-9 {
    width: 74.6%;
}

.column-10 {
    width: 83.0666666667%;
}

.column-11 {
    width: 91.5333333333%;
}

.column-12 {
    width: 100%;
    margin-left: 0;
}

@media screen and (max-width:$max-width) {
    .column-1, 
    .column-2, 
    .column-3, 
    .column-4, 
    .column-5, 
    .column-6, 
    .column-7, 
    .column-8, 
    .column-9, 
    .column-10, 
    .column-11, 
    .column-12 {
        float: none;
        width: auto;
    }
  
    .column + .column {
        margin-left: 0;
    }
}


//--------------
//  Grid styling
//--------------

.container {
    padding: 20px;
    
    @media(min-width:$min-width-lg) {
        margin: 0 auto;
        width: 1300px;
    }
}

.column {
    padding: 5px;
    min-height: 30px;
}

.row {
    margin-bottom: 10px;
}

.row:last-child {
    margin-bottom: 0;
}

@media(max-width:$max-width) {
    .row {
        margin-bottom: 0;
    }
    .column {
        margin-bottom: 10px;
    }
    .row:last-child .column:last-child {
        margin-bottom: 0;
    }
}